.account-information-theme1 {
  width: 100%;
  .acc-info {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    border-bottom: 2px solid #d9d9d9;
    padding: 24px 0 24px 0px;
    margin-bottom: 69px;
    margin-left: 28px;
  }

  .fnb-input-theme {
    height: 100%;
    font-size: 18px;

    .ant-input {
      font-size: 18px;
    }
  }
  .fnb-input[disabled] {
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    background: #f7f7f7;
    border: none;
    cursor: not-allowed;
    opacity: 1;
  }
  .my-profile-img {
    width: 240px;
    height: 240px;
    position: relative;
    margin: 0 auto 30px;
    border-radius: 12px;
    img {
      width: 240px;
      height: 240px;
      object-fit: cover;
      border-radius: 12px;
    }
    .camera-upload-img {
      position: absolute;
      top: 15px;
      right: 15px;
      &:hover {
        cursor: pointer;
      }
    }
    svg {
      height: 30px;
      width: 27px;
    }
  }
  .personal-info {
    margin: 0 auto;
    .personal-info-fullname,
    .personal-info-phone-code,
    .personal-info-phone-number,
    .personal-info-email {
      .ant-form-item-control-input-content {
        height: 60px;
      }
    }

    .personal-info-birth-day{
      .fnb-date-picker .ant-picker-input{
        flex-direction: row-reverse;
      }
    }

    .ant-row {
      row-gap: 40px !important;
    }

    .ant-form-item {
      margin-bottom: 0;
    }

    .info-title {
      text-align: end;
      margin: auto !important;
      padding-right: 72px !important;
    }

    .personal-info-phone-code {
      .ant-select {
        height: 100%;
        .ant-select-selector {
          height: 100%;
          align-items: center;
          display: flex;
          justify-content: center;
          margin: auto;
          font-size: 18px;
        }
      }
    }
    .ant-form-item-explain-error {
      position: absolute;
      margin-top: 8px;
    }
    .css-dev-only-do-not-override-15rg2km {
      row-gap: 40px !important;
    }
    .ant-row .ant-col-lg-20 .ant-form-item {
      margin-bottom: 0px;
    }
    .ant-row .ant-col {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
    }
    .account-info-save {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: #fef7ed;
      width: 214px;
      height: 60px;
      border-radius: 12px;
      border: none;
      outline: none;
      margin: 24px 0;
      float: right;
      position: relative;
      right: 7%;
      background-color: #026f30 !important;
    }
    .ant-picker-input input {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      color: #000000;
    }

    .personal-info-phone-number {
      padding-right: 20px !important;
    }

    .exist-phone-code {
      color: #ff4d4f;
      margin-left: 8px;
    }
    .personal-info-gender {
      span {
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 23px;
        color: #000000;
      }
      .ant-form-item {
        margin-bottom: 0px;
      }
      .ant-radio > span {
        margin-right: 10px;
      }
      .ant-radio ~ span {
        margin-right: 40px;
      }

      .ant-radio-wrapper .ant-radio-inner {
        background: #ffffff;
        border: 1px solid #a5abde;
        width: 24px;
        height: 24px;
      }

      .ant-radio-wrapper .ant-radio-checked .ant-radio-inner {
        border: 1px solid #ff8c21;
      }

      .ant-radio-wrapper .ant-radio-inner::after {
        background: #ff8c21;
      }

      .ant-radio-wrapper .ant-radio-checked .ant-radio-inner::after {
        transform: scale(-0.95);
      }
    }
  }
  .select-personal-info-dropdown {
    width: 100%;
    margin-right: 24px;
    .ant-select-selector:hover {
      border: none !important;
    }
    .ant-select-selector {
      border-radius: 12px !important;
      border: none !important;
      box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25) !important;
      background: #ffffff !important;
      height: 52px !important;
      cursor: pointer !important;
      .ant-select-selection-search {
        .ant-select-selection-search-input {
          height: 100% !important;
          font-size: 18px !important;
          padding-left: 5px !important;
        }
      }
      .ant-select-selection-placeholder {
        font-size: 18px !important;
        padding-left: 5px !important;
        margin: auto;
      }
      .ant-select-selection-item {
        font-style: normal !important;
        font-weight: 400 !important;
        font-size: 18px !important;
        line-height: 60px !important;
        letter-spacing: 0.3px !important;
        color: #000000 !important;
        padding-left: 5px !important;
      }
    }
    .ant-select-arrow {
      user-select: all !important;
      padding-right: 5px !important;
    }
  }
}

/* Mobile */
@media (max-width: 740px) {
  .account-information-theme1 {
    width: calc(100% - 32px);
    margin: 0 16px;
    .arrow-left-title {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: 0.3px;
      color: #000000;
      display: flex;
      svg {
        margin-right: 20px;
      }
    }
    .my-profile-account {
      justify-content: center;
      margin-top: 19px;
    }
    .personal-info {
      width: 100%;
      margin: unset;

      .ant-row {
        row-gap: 16px !important;
      }

      .info-title {
        text-align: start;
        padding-right: 72px !important;
      }

      .personal-info-fullname,
      .personal-info-email,
      .personal-info-gender,
      .personal-info-phone-code,
      .personal-info-phone-number {
        margin-top: 8px !important;
      }

      .account-info-save {
        width: 100%;
        right: 0;
      }
      .css-dev-only-do-not-override-15rg2km {
        row-gap: 24px !important;
      }
      .personal-info-phone-code {
        .fnb-input-customize {
          padding-left: 5px;
        }
      }
      .ant-picker-input input {
        font-size: 16px;
        line-height: 20px;
      }
      .personal-info-phone-number {
        padding-right: 8px !important;
      }
      .personal-info-gender {
        span {
          font-family: "Plus Jakarta Sans";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 20px;
          color: #000000;
        }
        .ant-form-item {
          margin-bottom: 0px;
        }
        .ant-radio > span {
          margin-right: unset;
        }
        .ant-radio ~ span {
          margin-right: unset;
        }
        .ant-radio-group {
          width: 100%;
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }
}
