.edit-product-cart-detail-container {
  .product-detail-content {
    background: #ffffff;
    border-radius: 16px;
    padding: 32px;
    margin: 0 auto;
    .product-detail-content-left {
      padding-right: 16px;
    }
    .product-detail-content-right {
      padding-left: 16px;
      .ant-collapse > .ant-collapse-item > .ant-collapse-header {
        align-items: center;
      }
    }
    .product-detail-title {
      font-size: 48px;
      font-weight: 700;
      line-height: 60px;
      text-align: left;
      color: #026f30;
      margin-bottom: 42px;
    }
    .product-price {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 32px;
      line-height: 40px;
      margin: auto;
      letter-spacing: 0.3px;
    }
    .product-original-price {
      font-weight: 700;
      font-size: 20px;
      line-height: 25px;
      margin: auto;
      letter-spacing: 0.3px;
      text-decoration-line: line-through;
      color: #b2b2b2;
      margin-left: 16px;
    }
    .product-price-name {
      font-size: 20px;
      line-height: 25px;
      margin: auto;
      letter-spacing: 0.3px;
      color: #b2b2b2;
    }
    .modify-quantity {
      float: right;
      .btn-reduce {
        width: 52px;
        height: 52px;
        min-width: unset !important;
        border-radius: 8px;
        float: right;
        border: 0px !important;
      }
      .btn-increase {
        width: 52px;
        height: 52px;
        min-width: unset !important;
        border-radius: 8px;
        float: right;
        border: 0px !important;
      }
      .quantity-product {
        font-size: 24px;
        font-weight: 700;
        line-height: 30px;
        align-items: center;
        padding: 14px 40px;
        height: 52px;
        float: right;
      }
    }
    .note {
      display: flex;
      margin-top: 38px;
      .product-detail-input {
        min-height: 80px;
        border-radius: 16px;
        font-weight: 400;
        font-size: 24px;
        line-height: 30px;
        padding: 20px 0px 20px 50px;
        border-radius: 12px;
        border: 1px solid #6d6d6d;
      }
      :where(.css-dev-only-do-not-override-15rg2km).ant-input-affix-wrapper {
        padding: 0;
      }
      .ant-input-affix-wrapper {
        border-radius: 16px;
        .ant-input-prefix {
          background: #e1e1e1;
          padding: inherit;
          border-radius: 16px;
          margin-right: 24px;
          width: 80px;
          text-align: center;
          justify-content: center;
        }
      }

      .note-icon-theme1 {
        width: 70px;
        position: relative;
        margin: 0 auto;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
        background-color: #E1E1E1;
        border-top-left-radius: 16px;
        border-bottom-left-radius: 16px;
        border: 1px solid #969696;
        border-right: none;
        .note-icon {
          position: relative; 
          left: 6px;
          top: 0px !important;
        }
        .border {
          height: 100%;
          width: 20px;
          position: absolute;
          right: -12px;
          border-top-right-radius: 16px;
          border-bottom-right-radius: 16px;
          background-color: #E1E1E1;;
        }
      }
      .input-text-area-theme1 {
        flex: 1;
        .product-detail-input {
          border-top-left-radius: 0px;
          border-bottom-left-radius: 0px;
          border: 1px solid #969696;
          border-left: none;
          padding-left: 24px;         
        }
      }
    }
    .btn-product-detail {
      width: 100%;
      height: 80px;
      border-radius: 16px;
    }
    .btn-submit {
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      color: #ffffff;
      background: #026f30;
      border: 0px;
      margin-top: 32px;
    }
    .btn-favorite-product {
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
      color: #000000;
      background: #ffffff;
      border: 1px solid #969696;
      margin-top: 16px;
      margin-bottom: 32px;
    }
    .product-detail-image {
      max-width: 100% !important;
      margin-bottom: 32px;
      .promotional-product {
        display: flex;
        position: absolute;
        width: 350px;
        height: 72px;
        left: -30px;
        top: 0px;
        background: #e91717;
        transform: rotate(-25deg);
        z-index: 1;
        align-items: center;
        span {
          font-family: "Plus Jakarta Sans";
          font-style: normal;
          font-weight: 700;
          font-size: 32px;
          line-height: 40px;
          letter-spacing: 0.3px;
          text-align: center;
          color: #ffffff;
          align-items: center;
          padding-left: 70px;
        }
      }
    }
    .product-detail-rate {
      font-size: 24px;
      color: #dc710e;
      vertical-align: bottom;
    }
    .total-review {
      font-weight: 400;
      font-size: 24px;
      line-height: 30px;
      color: #494949;
      margin-left: 16px;
    }
    .description-title {
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      color: #494949;
      margin-top: 32px;
      margin-bottom: 12px;
    }
    .description-content {
      font-weight: 400;
      font-size: 24px;
      line-height: 30.24px;
      color: #494949;
    }
    .options {
      margin-top: 32px;
      .ant-collapse-item.ant-collapse-item-active.product-price-header {
        > .ant-collapse-header {
          background-color: #f7931e;
          border-radius: 15px !important;
          border: none;
          > .ant-collapse-header-text {
            color: #ffffff;
          }
        }
        > .ant-collapse-content.ant-collapse-content-active {
          border-top: none;
        }
      }
      .ant-collapse-item.product-price-header {
        > .ant-collapse-header {
          background-color: #f7931e;
          border-radius: 15px !important;
          border: none;
          > .ant-collapse-header-text {
            color: #ffffff;
          }
        }
        > .ant-collapse-content {
          border-top: none;
        }
        > .ant-collapse-content.ant-collapse-content-active > .ant-collapse-content-box {
          border: none;
          padding: 33px 0px 0px 0px !important;
        }
        > .ant-collapse-content > .ant-collapse-content-box {
          border: none;
          padding: 33px 0px 0px 0px !important;
        }
      }
      .product-price-header {
        border-radius: 15px;
      }
      .product-price-collapse {
        border: none;
        margin-bottom: 25px;
      }
      .product-price-collapse:last-child {
        margin-bottom: 0px;
      }
      .product-detail-collapse {
        border-radius: 15px;
        .ant-collapse-header-text {
          font-weight: 700;
          font-size: 24px;
          line-height: 30px;
          color: #6a6a6a;
        }
        .ant-collapse-header {
          min-height: 66px;
          height: 100%;
          background: #f3f3f3;
        }
        .ant-collapse-item:last-child {
          border-radius: 0 0 15px 15px;
          .ant-collapse-content {
            border-radius: 0 0 15px 15px;
          }
        }
      }
      .ant-radio-button-wrapper:not(:first-child)::before {
        background: none;
      }
      .radio-option {
        color: #969696;
        font-weight: 400;
        font-size: 24px;
        line-height: 24px;
        height: 100%;
        padding: 16px;
        border-radius: 8px;
        border: 1px solid #9f9f9f;
        .radio-option-price {
          margin-bottom: 0px !important;
          padding-top: 10px;
        }
      }
      .ant-radio-button-wrapper-checked {
        background-color: #026f30;
        color: #ffffff;
      }
    }

    .options .ant-collapse-content-box > .ant-radio-group.ant-radio-group-outline.ant-radio-group-large {
      display: flex;
      gap: 16px;
      flex-wrap: wrap;
    }

    .options .ant-collapse-content-box {
      padding: 32px 16px !important;
    }
    .topping > .ant-collapse-content > .ant-collapse-content-box {
      padding: 0px !important;
    }
  }
  .similar-product {
    .similar-product-title {
      margin-top: 124px;
      margin-bottom: 124px;
      text-align: center;
      font-weight: 700;
      font-size: 60px;
      line-height: 76px;
      color: #026f30;
    }
  }
  .review-content {
    border: 1px solid #d9d9d9;
    border-radius: 16px !important;
    margin-top: 32px !important;
    .ant-card-head-title {
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
    }
    .ant-card-head {
      border-bottom: 1px solid #d9d9d9;
    }
    .review-icon {
      margin: auto;
    }
    .review-text {
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      margin-top: 10px;
    }
    .ant-card-body {
      text-align: center;
    }
  }
}
.choose-options-header {
  display: none;
}
.d-none {
  display: none !important;
}
.d-block {
  display: block !important;
}
.popup-choose-options {
  display: block;
}
.btn-hide-show-popup-when-mobile {
  display: none;
}

