.options {
  .topping-item {
    font-size: 24px;
    line-height: 24px;
    border-top: 1px dashed #000000;
    padding: 26px 0px;
    margin-left: 26px;
    margin-right: 26px;
    .topping-name {
      font-size: 24px !important;
      line-height: 24px !important;
      font-weight: 400;
    }
    .topping-price {
      font-size: 24px !important;
      line-height: 24px !important;
      margin-top: 10px;
      font-weight: 700;
      margin-bottom: 0px !important;
    }
    .topping-original-price {
      font-family: 'Plus Jakarta Sans';
      font-style: normal;
      font-weight: 400;
      font-size: 20px !important;
      line-height: 24px !important;
      display: flex;
      align-items: center;
      letter-spacing: 0.3px;
      text-decoration-line: line-through;
      color: #C1C1C1;
      margin-top: 10px;
    }
    .modify-quantity {
      float: right;
      .btn-reduce {
        width: 52px;
        height: 52px;
        min-width: unset !important;
        border-radius: 8px;
        float: right;
        border: 0px !important;
      }
      .btn-increase {
        width: 52px;
        height: 52px;
        min-width: unset !important;
        border-radius: 8px;
        float: right;
        border: 0px !important;
      }
      .quantity-product {
        font-size: 24px;
        font-weight: 700;
        line-height: 30px;
        align-items: center;
        padding: 14px 40px;
        height: 52px;
        float: right;
      }
    }
  }
  .topping-item:first-child {
    border-top: 0px !important;
  }
}

/* Mobile - ant-xs */
@media (max-width: 575px) {
  .options {
    .topping-item {
      font-size: 16px;
      line-height: 24px;
      border-top: 1px dashed #000000;
      padding: 16px 0px;
      margin-left: 16px;
      margin-right: 16px;
      .topping-name {
        font-size: 16px !important;
        line-height: 24px !important;
        font-weight: 400;
      }
      .topping-price {
        font-size: 16px !important;
        line-height: 24px !important;
        margin-top: 10px;
        font-weight: 700;
        margin-bottom: 0px !important;
      }
      .modify-quantity {
        float: right;
        width: 100%;
        margin: auto;
        .btn-reduce {
          width: 36px !important;
          height: 36px !important;
          min-width: unset !important;
          border-radius: 8px;
          float: right;
          border: 0px !important;
          svg {
            width: 36px !important;
            height: 36px !important;
          }
        }
        .btn-increase {
          width: 36px !important;
          height: 36px !important;
          min-width: unset !important;
          border-radius: 8px;
          float: right;
          border: 0px !important;
          svg {
            width: 36px !important;
            height: 36px !important;
          }
        }
        .quantity-product {
          font-weight: 700;
          font-size: 16px;
          line-height: 20px;
          align-items: center;
          padding: 8px 28px;
          height: 36px;
          float: right;
        }
      }
    }
    .topping-item:first-child {
      border-top: 0px !important;
    }
  }
}
