#themeBestSellingProduct {
  scroll-margin-top: 120px;
  max-width: 1744px;
  margin: 0 auto;
  .title-best-selling-product-list {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 76px;
    letter-spacing: 0.3px;
    text-align: center;
    padding-top: 80px;
  }
  .product-list-theme-1 {
    width: 100%;
    padding-top: 48px;
    padding-bottom: 190px;
    .swiper-slide {
      width: 320px !important;
      scale: 1.05;
    }
    .swiper-slide-active {
      padding-top: 241px;
      scale: 1.25;
    }
    .swiper-slide-prev {
      padding-top: 161px;
      transform-origin: left;
      scale: 1.11;
    }
    .swiper-slide-next {
      padding-top: 161px;
      transform-origin: right;
      scale: 1.11;
    }
    .product-card {
      display: flex !important;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      height: 445px;
      .product-name {
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 45px;
        color: #000;
        max-width: 290px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        text-align: center;
        margin: 10px 0;
      }
      .product-price {
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 60px;
        letter-spacing: 0.3px;
        .discount-price {
          text-decoration: line-through;
          color: #898a8d;
          margin-left: 8px;
        }
      }
      .product-image-cover {
        width: 320px;
        height: 320px;
        .product-image {
          border-radius: 10px;
          width: 320px;
          height: 320px;
          background-repeat: no-repeat;
          background-position: center center;
        }
      }
    }
    .horizontalImg {
      background-size: auto 100%;
    }
    .verticalImg {
      background-size: 100% auto;
    }
    .fitSizeImg {
      background-size: 100% 100%;
    }
    .flash-sale-border {
      border: 5px solid #ff5406;
      border-radius: 14px;
    }
  }

  /* Mobile */
  @media (max-width: 740px) {
    .title-best-selling-product-list {
      font-size: 24px;
      line-height: 30px;
      padding-top: 16px;
      padding-bottom: 24px;
    }
    .product-list-theme-1 {
      padding-bottom: 16px;
      padding-left: 16px;
      padding-top: 0px;
      .swiper-slide {
        width: 160px !important;
        scale: 1;
      }

      .swiper-slide-active,
      .swiper-slide-next,
      .swiper-slide-prev {
        padding-top: 0;
        scale: 1;
      }

      .product-card {
        height: 237px;
        .product-image-cover {
          width: 160px;
          height: 160px;
          .product-image {
            border-radius: 12px;
            width: 160px;
            height: 160px;
            background-repeat: no-repeat;
            background-position: center center;
            .fnb-display-image {
              border-radius: 12px;
              .display-image {
                border-radius: 12px;
              }
            }
          }
        }
        .product-name {
          font-size: 15px;
          line-height: 19px;
          margin: 10px 0;
        }
        .product-price {
          font-size: 15px;
          line-height: 19px;
        }
      }
    }
  }
}
