$margin_top: 30px;
$padding_login: 20px;
.login_page_theme1_container {
  padding-top: 100px;
  padding-bottom: 100px;
}
.login_page_theme1 {
  width: 560px !important;
  height: 560px !important;
  background: #ffffff;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  margin: 0 auto;
  margin-right: 180px;
  width: 600px;
  height: fit-content;
  padding-top: 20px;
  padding-bottom: 120px;
  padding-left: 56px;
  padding-right: 56px;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.25);
  position: relative;
  .loading-overlay {
    position: absolute;
    z-index: 1000;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.7);
    .loading {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 1000;
    }
  }
  .login_title {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 52px;
    line-height: 66px;
    margin: 120px 0 70px;
  }
  .login_detail {
    margin-top: 73px;
    display: flex;
    flex-flow: row nowrap;
    .login_phone_icon {
      display: none;
    }
    .login_country {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
      color: #000000;
      display: flex;
      align-items: center;
      background: rgba(255, 255, 255, 0.7);
      border: 1px solid #6d6d6d;
      border-radius: 12px;
      width: 107px !important;
      height: 60px !important;
      .ant-select-selector {
        padding: 0 20px;
      }
      .login_country_popup {
        width: 600px;
      }
    }
    .login_phone {
      margin-left: 10px;
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
      color: #000000;
      height: 60px;
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
      background: rgba(255, 255, 255, 0.7);
      border: 1px solid #6d6d6d;
      border-radius: 12px;
    }
  }
  .login_phone_error {
    padding-left: 100px;
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    align-items: center;
    color: #ff0000;
  }
  .login_firebase_error {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    align-items: center;
    color: #ff0000;
  }
  .login_button_box {
    .login_button {
      width: 100%;
      height: 60px;
      opacity: 1;
      border-radius: 12px;
      margin-top: 60px;
      span {
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 23px;
      }
    }
  }
}

@media screen and (max-width: 639px) {
  .login_page_theme1_container {
    padding-top: 35px;
    padding-bottom: 29px;
    width: 100%;
    margin: 0 auto;
  }
  .login_page_theme1 {
    width: calc(100% - 32px) !important;
    height: 381px !important;
    margin: auto !important;
    padding: 30px 16px 52px;
    .login_title {
      font-size: 40px !important;
      line-height: 50px !important;
      margin-top: 45px !important;
      margin-bottom: 50px;
    }
    .login_detail {
      margin-top: 52px !important;
    }
    .login_button_box {
      .login_button {
        margin-top: 40px;
        margin-bottom: 16px;
      }
    }
  }
}
