:root {
  --heightHeader: 100px;
  --heightHeaderMobile: 60px;
}

body.disable-scroll {
  overflow: hidden;
}

.clear-both {
  clear: both;
}

.header-theme1-new {
  display: block;
  height: 100px;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}

.header-theme1-new .nav-bar {
  display: none;
}

.header-theme1-new .logo-original-theme {
  max-height: var(--heightHeaderMobile);
  max-width: 200px;
  object-fit: cover;
}

.header-theme1-new .overlay {
  position: fixed;
  top: 60px;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
  animation: fadeIn linear 0.1s;
}

#themeHeader.tc-on-focus {
  position: relative;
  top: -5px;
}

.theme1-header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  margin: 0 auto;
}
.logo-menu-contain {
  display: flex;
  align-items: center;
  .logo-contain {
    pointer-events: none;
  }
}
ul#nav-menu {
  margin-bottom: unset;
}
#nav-menu {
  height: 100px;
  margin-left: 102px;
  display: flex;
  list-style: none;
  gap: 40px;
}

#nav-menu li {
  position: relative;
}
#nav-menu > li:not(:last-child) {
  position: relative;
  max-width: 200px;
}

#nav-menu > li > a {
  font-family: "Plus Jakarta Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  text-decoration: none;
  line-height: 100px;
  overflow: hidden;
  max-height: 100px;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#nav-menu > li > a > svg {
  position: absolute;
  top: 43px;
}

#nav-menu li:hover .sub-nav-menu {
  display: flex;
  flex-direction: column;
}

#nav-menu .sub-nav-menu {
  display: none;
  position: absolute;
  top: 80px;
  left: -20px;
  background-color: #fff;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.25);
  filter: drop-shadow(0 3px 10px rgba(0, 0, 0, 0.25));
  border-radius: 8px;
  width: 327px;
  z-index: 2;
}

#nav-menu .sub-nav-menu::before {
  content: "";
  position: absolute;
  top: -15px;
  left: 50px;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  background-color: #fff;
  width: 15px;
  height: 15px;
}

#nav-menu .sub-nav-menu li {
  margin: 5px 12px;
  border-radius: 8px;
  height: 54px;
  line-height: 54px;
  list-style: none;
}

#nav-menu .sub-nav-menu li a {
  font-family: "Plus Jakarta Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 54px;
  color: #000;
  text-decoration: none;
  text-transform: capitalize;
  padding-left: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  border-radius: 8px;
}

#nav-menu .sub-nav-menu li:hover,
#nav-menu .sub-nav-menu li:hover a {
  color: #fff;
  text-decoration: none;
  opacity: 0.8;
}

.header-theme1-new .account-cart {
  display: flex;
  align-items: center;
  position: relative;

  .account-header-theme-1-popover {
    width: 327px;
    height: auto;
    border-radius: 8px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
  }
}

.header-theme1-new .account {
  cursor: pointer;
}

.header-theme1-new .account :hover {
  opacity: 0.8;
  cursor: pointer;
}

.header-theme1-new .account-cart .cart {
  cursor: pointer;
  z-index: 999;
  width: 30px;
}

.header-theme1-new .account-cart .cart :hover {
  opacity: 1;
}

.header-theme1-new .account-cart .cart .cart-quantity {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: #ed4c5c;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -20px;
  right: -20px;
  user-select: none;
}

.header-theme1-new .nav-input-header:checked ~ .overlay {
  display: block;
  z-index: 998;
}

.header-theme1-new .nav-input-header:checked ~ .nav-menu-mobile {
  transform: translateX(0%);
  opacity: 1;
  z-index: 999;
}

.cart-wrap {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.header-wrap {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
}
.d-none {
  display: none;
}

/* Header Mobile */

.header-theme1-new .nav-menu-mobile {
  position: fixed;
  top: 60px;
  bottom: 0;
  left: 0;
  width: 320px;
  max-width: 100%;
  background-color: #fff;
  transform: translateX(-100%);
  opacity: 0;
  transition: all linear 0.2s;
}

.header-theme1-new .nav-menu-mobile .close-nav-bar {
  position: absolute;
  top: 10px;
  right: 10px;
}

.header-theme1-new .nav-menu-mobile #nav-menu-mobile-list {
  margin: 9px 16px;
  list-style-type: none;
}

.header-theme1-new .nav-menu-mobile #nav-menu-mobile-list li {
  padding: 9px 0;
}

.header-theme1-new .nav-menu-mobile #nav-menu-mobile-list li a {
  font-family: "Plus Jakarta Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 32px;
  color: #000;
  text-decoration: none;
}

#nav-menu-mobile-list .sub-nav-menu-mobile {
  display: block;
}
#nav-menu-mobile-list .sub-nav-menu-mobile {
  list-style-type: none;
  margin-left: 36px;
}
#box-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart-header-theme-1-popover {
  .ant-popover-content {
    .ant-popover-arrow {
    }
    .ant-popover-inner {
    }
  }
}
@media (max-width: 1600px) {
  .header-theme1-new .account-cart {
    margin-right: 40px;
  }
}
/* Mobile */
@media (max-width: 740px) {
  #main {
    width: 100%;
  }
  #header .logo-original-theme {
    max-height: var(--heightHeaderMobile);
  }
  .header-theme1-new .account-cart {
    margin-right: unset;
  }
  .header-theme1-new {
    width: 100%;
    height: var(--heightHeaderMobile);
    position: unset;
    top: unset;
    z-index: unset;
  }
  .header-theme1-new .logo-original-theme {
    max-width: 219px;
  }
  .header-theme1-new .account-cart {
    position: relative;

    .account-header-theme-1-popover {
      width: 100vw;
      height: auto;
      border-radius: 0px;
      padding: 16px;
      .ant-popover-content {
        .ant-popover-arrow {
          left: 80%;
        }
        .ant-popover-inner {
        }
      }
    }
  }

  .header-theme1-new .account-cart .cart .cart-quantity {
    width: 20px;
    height: 20px;
    position: absolute;
    top: -5px;
    right: -10px;
    font-size: 14px;
  }
  .header-theme1-new .nav-bar {
    display: inline-block;
  }
  .header-theme1-new .logo {
    max-width: 220px;
    margin-right: 5px;
    line-height: var(--heightHeaderMobile);
  }
  #themeHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: var(--heightHeaderMobile);
  }
  .theme1-header {
    position: unset;
    top: unset;
    width: 100%;
    max-width: 100%;
    z-index: unset;
    margin: unset;
  }
  .login_content_theme1 {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    align-items: center;
    color: #000000;
    cursor: pointer;
  }
  .logout_content_theme1 {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    align-items: center;
    color: #000000;
    cursor: pointer;
  }
  #nav-menu {
    display: none;
  }
  #nav-menu-mobile-list li {
    position: relative;
  }

  #nav-menu-mobile-list .arrow-right-mobile {
    position: absolute;
    right: 0px;
  }
  #nav-menu-mobile-list .menu_arrow_rotate {
    transform: rotate(90deg);
    transition: transform 0.4s;
  }
  #nav-menu-mobile-list .menu_arrow_rotate_reset {
    transform: rotate(0deg);
    transition: transform 0.4s;
  }
  #nav-menu-mobile-list .menu_arrow_hide {
    display: none;
  }
  .header-theme1-new .account-cart .cart {
    margin: 0 0 0 15px;
    width: 20px;
  }
  .header-theme1-new .account-cart .account svg {
    width: 24px;
    display: block;
  }
  .header-theme1-new .account-cart .cart svg {
    width: 20px;
    display: block;
  }
  #box-content {
    margin: 0 16px;
    width: 100%;
    .nav-bar {
      margin-right: 20px;
    }
    .nav-bar-active {
      transform: rotate(90deg);
    }
    .logo-contain {
      flex-grow: 1;
      max-height: var(--heightHeaderMobile);
    }
  }

  .cart-header-theme-1-popover {
    position: absolute;
    top: 65px !important;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
