.ant-popover-content{
  height: 88px;
  .ant-popover-inner
  {
    .ant-popover-inner-content{
      .login_content_theme1 {
        .icon {
          position: relative;
          top: 5px;
          left: 5px;
          cursor: pointer;
        }
        .title {
          font-family: "Plus Jakarta Sans";
          font-style: normal;
          font-weight: 400;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
          color: #000000;
          cursor: pointer;
          margin-top: -25px;
        }
      }
    }
  }
}


@media screen and (max-width: 639px) {
  .ant-popover-content{
    height: 56px !important;
    width: 100vw !important;
    margin-left: -16px !important;
    margin-top: -28px !important;
  }
}