.category-list-homepage {
  padding: 100px 0px 100px;
  background: #fff;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  .slick-arrow.slick-prev,
  .slick-arrow.slick-next {
    width: 48px;
    height: 48px;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 12px;
    z-index: 1;
  }
  .slick-prev:before,
  .slick-next:before {
    opacity: 1;
  }
  .slick-list {
    .slick-slide {
      display: flex;
      justify-content: center;
    }
  }
  .slick-prev::before {
    content: url("../assets/icons/left-category.svg") !important;
  }
  .slick-next::before {
    content: url("../assets/icons/right-category.svg") !important;
  }
  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }
  .button-left-arrow {
    background-image: url("../assets/icons/left-category.svg") !important;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 12px;
    height: 48px;
    width: 48px;
    top: 46%;
    position: absolute;
    z-index: 2;
    left: -50px;
  }
  .button-right-arrow {
    background-image: url("../assets/icons/right-category.svg") !important;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 12px;
    height: 48px;
    width: 48px;
    top: 46%;
    position: absolute;
    z-index: 2;
    right: -50px;
  }
  .swiper-wrapper {
    .swiper-slide:first-child {
      margin-left: 2px;
    }
  }
  .category-card {
    width: 100%;
    min-width: 248px;
    max-width: 248px;
    height: 323px;
    background-color: #fff;
    border: 1px solid #dddddd;
    border-radius: 16px;
    position: relative;
  }
  .category-image {
    width: 100%;
    height: 152px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-image: none;
      border-radius: 16px 16px 0 0;
    }
  }
  .category-title {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 0.3px;
    margin: 16px 24px 8px;
  }
  .category-description {
    font-family: "Plus Jakarta Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.3px;
    margin: 8px 24px 12px;
  }
  .category-order-btn {
    width: 100%;
    height: 55px;
    border-radius: 0px 0px 16px 16px;
    padding: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    text-align: center;
    text-decoration: none;
    position: absolute;
    bottom: 0;
    outline: none;
    &:hover {
      cursor: pointer;
    }

    .text-btn {
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      letter-spacing: 0.3px;
      color: #ffffff;
      text-decoration: none;
      padding: 0 24px;
    }
  }
}

/* Mobile */
@media (max-width: 740px) {
  .category-list-homepage {
    padding: 16px 0px;
    .slick-arrow.slick-prev,
    .slick-arrow.slick-next {
      display: none !important;
    }
    .regular.slider {
      display: flex;
      overflow: auto;
      width: calc(100% - 8px);
      transition: transform 0.3s ease 0.1s;
      margin-left: 8px;
    }

    /* Hide scrollbar for Chrome, Safari and Opera */
    .regular.slider::-webkit-scrollbar {
      display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox */
    .regular.slider {
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
    }

    .category-card {
      width: 100% !important;
      min-width: 180px !important;
      max-width: 180px !important;
      height: 224px;
      border-radius: 12px;
      margin-right: 8px;
    }
    .category-image {
      width: 100%;
      height: 100px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 12px 12px 0 0;
      }
    }
    .category-title {
      font-size: 16px;
      line-height: 20px;
      margin: 10px 13px;
    }
    .category-description {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      margin: 0px 13px 12px;
    }
    .category-order-btn {
      height: 36px;
      border-radius: 0px 0px 12px 12px;

      .text-btn {
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 0.3px;
        color: #ffffff;
        text-decoration: none;
        padding: 0 13px;
      }
    }
    .button-left-arrow,
    .button-right-arrow {
      display: none;
    }
    .swiper-wrapper {
      .swiper-slide:first-child {
        margin-left: 4px;
      }
    }
  }
}

.text-line-clamp-1 {
  overflow: hidden;
  display: -webkit-box !important;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.text-line-clamp-2 {
  overflow: hidden;
  display: -webkit-box !important;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
