.fnb-display-image-checkout {
  width: 100%;
  padding-top: 100%;
  position: relative;
  border-radius: 10px;
  .display-image-checkout {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    padding: 4px;
  }
  .flash-sale-checkout {
    background-image: url("../../assets/images/background-flash-sale.png");
    width: 113px;
    height: 24px;
    border-top-right-radius: 6px;
    position: absolute;
    bottom: 0px;
    left: -4px;
    padding: 4px 16px 4px 16px;
    .flash-sale-logo-checkout {
      width: 100%;
      height: 100%;
    }
  }
  .promotion-label-checkout {
    position: absolute;
    top: 0px;
    left: -32px;
    background-color: #ff3448;
    transform: rotate(-25deg);
    width: 150px;
    height: 25px;
    z-index: 1;
    span {
      display: flex;
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      letter-spacing: 0.3px;
      text-align: center;
      color: #fff;
      height: 100%;
      margin-left: 40px;
      align-items: center;
    }
  }
  .promotion-label-checkout-max-value {
    position: absolute;
    top: 0px;
    left: -32px;
    background-color: #ff3448;
    transform: rotate(-25deg);
    width: 150px;
    height: 25px;
    z-index: 1;
    span {
      display: flex;
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 60%;
      line-height: 23px;
      letter-spacing: 0.3px;
      text-align: center;
      color: #fff;
      height: 100%;
      margin-left: 30px;
      align-items: center;
    }
  }
}

/* Mobile */
@media (max-width: 575px) {
  .fnb-display-image-checkout {
    .flash-sale-checkout {
      background-image: url("../../assets/images/background-flash-sale.png");
      width: 61px;
      height: 13px;
      border-top-right-radius: 6px;
      position: absolute;
      bottom: -2px;
      left: -2px;
      padding: 2px 8px 2px 8px;
      .flash-sale-logo-checkout {
        width: 44px;
        height: 8px;
        //object-fit: contain;
        top: -8px;
        position: relative;
      }
    }
    .promotion-label-checkout {
      position: absolute;
      top: 4px;
      left: -27px;
      object-fit: cover;
      background-color: #ff3448;
      transform: rotate(-25deg);
      width: 90px;
      height: 14px;
      z-index: 1;
      span {
        display: flex;
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 0.3px;
        text-align: center;
        color: #fff;
        height: 100%;
        margin-left: 30px;
        align-items: center;
      }
    }
  }
}
