.similar-product-content {
  width: 100%;
  padding-left: 5px;
  .swiper-button-prev,
  .swiper-button-next {
    width: 48px;
    height: 48px;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 12px;
    z-index: 1;
  }
  .swiper-button-prev:after,
  .swiper-button-next:after {
    opacity: 1;
  }
  .slick-list {
    .slick-slide {
      display: flex;
      justify-content: center;
    }
  }
  .swiper-button-prev::after {
    content: url("../../../assets/icons/left-category.svg") !important;
  }
  .swiper-button-next::after {
    content: url("../../../assets/icons/right-category.svg") !important;
  }
  .swiper-slide {
    width: auto;
    margin-right: 52px !important;
    height: unset;
  }
  .product-card {
    width: 245px;
    border-radius: 14px;
    display: flex !important;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background: #ffffff;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.25);
    margin-bottom: 10px;
    .product-list-image.flash-sale-border,
    .product-list-image {
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 0px;
      .display-image {
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
      }
    }
    .product-image {
      width: 340px;
      height: 340px;
    }
    .product-card-content {
      padding: 16px 12px;
      .product-name {
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 28px;
        height: 52px;
        color: #000;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .star-vote {
        margin-top: 16px;
      }
      .product-price {
        margin-top: 16px;
        font-family: "Plus Jakarta Sans";
        font-style: normal;
        font-weight: 700;
        display: flex;
        align-items: end;
        flex-wrap: wrap;
        align-content: flex-start;
        height: 53px;
        .selling-price {
          float: left;
          font-size: 24px;
          line-height: 30px;
          letter-spacing: 0.3px;
          color: #026f30;
          margin-right: 7px;
        }
        .original-price {
          font-weight: 400;
          font-size: 18px;
          line-height: 23px;
          color: #c1c1c1;
          letter-spacing: 0.3px;
          text-decoration-line: line-through;
          margin-left: 2px;
        }
      }
    }
    .add-to-cart {
      background: #f7931e;
      border-radius: 6px;
      width: 221px;
      height: 40px;
      font-family: "Plus Jakarta Sans";
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 18px;
      align-items: center;
      text-align: center;
      letter-spacing: 0.3px;
      color: #ffffff;
      margin-bottom: 16px;
    }
  }
  .swiper-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal {
    display: none;
  }
  .flash-sale-border {
    border: 5px solid #ff5406;
    border-radius: 14px;
  }
}

/* Mobile */
@media (max-width: 575px) {
  .title-product-list {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 32px;
  }
  .similar-product-content {
    padding-left: 0px;
    position: unset;
    .swiper-slide {
      margin-right: 16px !important;
    }
    .product-card {
      width: 180px;
      .product-name {
        font-size: 16px !important;
        line-height: 20px !important;
        height: 40px !important;
        margin-top: 16px;
      }
      .product-price {
        overflow: hidden;
        height: 45px !important;
        .selling-price {
          font-size: 16px !important;
          line-height: 25px !important;
        }
        .original-price {
          font-size: 12px !important;
          line-height: 20px !important;
        }
      }
      .add-to-cart {
        width: 140px !important;
        height: 32px !important;
        border-radius: 8px !important;
        font-style: normal !important;
        font-weight: 500 !important;
        font-size: 16px !important;
        line-height: 16px !important;
      }
      .product-list-image {
        .flash-sale {
          margin-left: -1px;
          padding: 1.5px 12px !important;
          width: unset !important;
          .flash-sale-logo {
            margin-left: unset !important;
            margin-top: unset !important;
            width: 100px !important;
            height: 16px !important;
          }
        }
      }
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 20px !important;
    height: 20px !important;
    border-radius: 4px !important;
  }
  .swiper-button-prev {
    left: 0px;
  }
  .swiper-button-next {
    right: 0px;
  }
  .similar-product-content .swiper-button-prev::after {
    content: url("../../../assets/icons/left-responsive.svg") !important;
  }
  .similar-product-content .swiper-button-next::after {
    content: url("../../../assets/icons/right-responsive.svg") !important;
  }
}
